import { Note } from './blocks'

export class Site {
  constructor(id, updateCallback) {
    this.id = document.getElementById(id)
    this.update = updateCallback

    this.init() //  –∑–∞–ø—É—Å–∫–∞–µ–º —Å—Ä–∞–∑—É –ø—Ä–∏ –≤—ã–∑–æ–≤–µ new Site
  }

  render(model) {
    this.id.innerHTML = ''  // —É–¥–∞–ª—è–µ–º –≤—Å–µ —Å–æ–¥–µ—Ä–∂–∏–º–æ–µ id
    model.forEach(block => {
      this.id.insertAdjacentHTML('beforeend', block.toHTML()) 
    })
  }

  init() {
    this.id.addEventListener('submit', this.add.bind(this))
  }

  add(event) {
    event.preventDefault()

    const text = event.target.text.value
    const newNote = new Note(text)

    this.update(newNote)

    event.target.text.value = ''   // –æ—á–∏—â–∞–µ–º –ø–æ–ª—è –≤–≤–æ–¥–∞
  }
}