import { Site } from '../classes/site'
import { Notes } from './notes'

export class App {
  constructor(model) {
    this.model = model
  }
  init() {
    const updateCallback = newNote => {  // –≤—ã–∑—ã–≤–∞–µ—Ç—Å—è –ø—Ä–∏ —Å–æ–±—ã—Ç–∏–∏ –¥–æ–±–∞–≤–ª–µ–Ω–∏—è –∑–∞–º–µ—Ç–∫–∏
      delete this.model[2]   // —É–¥–∞–ª—è–µ—Ç —ç–ª-—Ç –º–∞—Å—Å–∏–≤–∞ –∏ –Ω–µ –ø–µ—Ä–µ–∑–∞–ø–∏—Å—ã–≤–∞—è –∏–Ω–¥–µ–∫—Å—ã (—É–¥–∞–ª—è–µ–º –∑–∞–≥–ª—É—à–∫—É)

      this.model.push(newNote)
      site.render(this.model)
    }

    const site = new Site('site', updateCallback)
    site.render(this.model)

    new Notes('notes')
  }
}